import { SplineData, MapData, PathFindingResultData, RenderType, NavigationColors, LocationTypeName, LocationDisplayType, Vec3 } from './models.js'; // 
import { scale, rotation, pan } from "./control.js";
import { groupIndex, fromId, toId } from "./inputs.js";
import { attachControls } from "./control.js";


// adjust path as needed
export let canvasMain = null;
export let canvasOverlay = null;
export let ctx = null;
export let ctxOverlay = null;
let circlePos = null;
/** give the module a canva  let circlePos = null;s *after* it exists in the DOM */
export function initCanvas(_canvasMain, _canvasOverlay) {
    canvasMain = _canvasMain;
    canvasOverlay = _canvasOverlay;
    ctx = _canvasMain.getContext("2d");
    ctxOverlay = _canvasOverlay.getContext("2d");
    // Sync overlay canvas size to match main canvas
    canvasOverlay.width = canvasMain.width;
    canvasOverlay.height = canvasMain.height;
    // Clear any previous overlay content
    ctxOverlay.clearRect(0, 0, canvasOverlay.width, canvasOverlay.height);
    attachControls(canvasMain);        // wheel / pan / zoom handlers
}

export let pathFindingResultData = null;
let pathData = null;
export let layoutData = null;
export let mapData = null;


/* ---------- main draw loop ---------- */
export function drawMainCanvas() {
    if (!layoutData) return;          // nothing to draw yet

    ctx.save();
    ctx.setTransform(1, 0, 0, 1, 0, 0);
    ctx.clearRect(0, 0, canvasMain.width, canvasMain.height);

    // centre + pan + zoom
    // ctx.translate(canvas.width / 2 + pan.x, canvas.height / 2 + pan.y);

    // measure on‐screen size
    const dpr = window.devicePixelRatio || 1;
    const rect = canvasMain.getBoundingClientRect();
    // calculate the pivot in buffer‐pixel units
    const pivotX = (rect.width / 2 + pan.x) * dpr;
    const pivotY = (rect.height / 2 + pan.y) * dpr;

    // move the origin to the correct buffer‐unit pivot
    ctx.translate(pivotX, pivotY);
    ctx.rotate(rotation);
    ctx.scale(scale, scale);

    /* ----- draw layout ----- */
    for (let g = 0; g < layoutData.groups.length; g++) {
        if (g !== groupIndex) continue;                 // show one group
        const group = layoutData.groups[g];

        for (const layer of group.layers) {
            if (layer.renderType === RenderType.SurfaceShape) {
                drawSurface(layer, group);
            } else if (layer.renderType === RenderType.LineShape) {
                drawLine(layer, group);
            }
        }
    }
    // console.log("scale:", scale);

    const alpha = labelAlpha(scale);
    if (alpha > 0) {
        ctx.globalAlpha = alpha;
        /* ----- draw path‑finding surfaces (zoom‑dependent) ----- */
        if (pathFindingResultData) {
            for (const [groupId, floorResult] of Object.entries(pathFindingResultData.floors)) {

                if (getGroupIndex(groupId) != groupIndex) continue;
                const fromLoc = getMapLocation(floorResult.fromId);
                const toLoc = getMapLocation(floorResult.toId);
                // draw under current zoom so the polygons scale

                //only draw the surface if the location is transit
                if (LocationTypeName[fromLoc.locationType] == 'Stairs' || LocationTypeName[toLoc.locationType] == 'Elevator') {
                    drawMapLocationSurface(fromLoc, NavigationColors[0]);   // base blue
                }
                if (LocationTypeName[toLoc.locationType] == 'Stairs' || LocationTypeName[toLoc.locationType] == 'Elevator') {
                    drawMapLocationSurface(toLoc, NavigationColors[0]);
                }
            }
        }
        // override colours for start / end
        let fromMapLocation = getMapLocation(fromId);
        let toMapLocation = getMapLocation(toId);

        if (fromMapLocation && getGroupIndex(fromMapLocation.groupId) === groupIndex) drawMapLocationSurface(fromMapLocation, NavigationColors[2]); //  
        if (toMapLocation && getGroupIndex(toMapLocation.groupId) === groupIndex) drawMapLocationSurface(toMapLocation, NavigationColors[1]); // red

        /* ----- draw path polyline (zoomed) ----- */
        if (pathFindingResultData) {
            for (const [groupId, floorResult] of Object.entries(pathFindingResultData.floors)) {

                if (getGroupIndex(groupId) != groupIndex) continue;
                const verts = floorResult.nodes;
                if (verts.length > 0) {
                    ctx.save();
                    ctx.lineWidth = 2 / scale;   // keep 2px on‑screen
                    ctx.strokeStyle = NavigationColors[0];
                    ctx.beginPath();
                    ctx.moveTo(verts[0].x, verts[0].z);
                    for (let k = 1; k < verts.length; k++) {
                        ctx.lineTo(verts[k].x, verts[k].z);
                    }
                    ctx.stroke();
                    // --- DEBUG: draw a small red dot at the first path node ---
                    // (removed for clarity; see animated circle below)
                    ctx.restore();
                }
            }
        }
        /* ---------------------------------------- */

        /* ----- draw icons and labels (rotate‑only, no scale) ----- */
        if (!mapData) return;
        for (const loc of mapData.locations) {
            if (getGroupIndex(loc.groupId) !== groupIndex) continue;

            // ICONS --------------------------------------------------
            if (loc.locationDisplayType === LocationDisplayType.Icon) {
                const entry = loadIcon(iconNameFor(loc));
                if (entry.loaded) {
                    // Project world centre → screen coords (pan + scale + rotate)


                    const size = 36;
                    ctx.save();
                    // Move to screen position, then un‑rotate so icon faces “north”
                    ctx.translate(loc.center.x, loc.center.z);
                    ctx.rotate(-rotation);
                    ctx.scale(1 / scale, 1 / scale); // 3. cancel zoom

                    ctx.drawImage(entry.img, -size / 2, -size / 2, size, size);
                    ctx.restore();
                }
            }

            // LABELS -------------------------------------------------
            if (loc.locationDisplayType === LocationDisplayType.Label) {
                const label = loc.name || LocationTypeName[loc.locationType];

                ctx.save();

                ctx.translate(loc.center.x, loc.center.z);
                ctx.rotate(-rotation);          // upright text
                ctx.scale(1 / scale, 1 / scale);

                ctx.font = "20px Arial";
                ctx.textAlign = "center";
                ctx.textBaseline = "middle";
                ctx.fillStyle = "#000";
                ctx.fillText(label, 0, 0);
                ctx.restore();
            }
        }
        ctx.restore();
    }
}

let distanceTraveled = 0; // world-space distance traveled along path
let segmentLengths = [];
let cumulativeLengths = [];
let totalPathLength = 0;
let speed = 0.3; // adjust as needed

function lerp(a, b, t) {
    return a + (b - a) * t;
}

function interpolate(p1, p2, t) {
    return {
        x: lerp(p1.x, p2.x, t),
        y: lerp(p1.z, p2.z, t)
    };
}
 
function animate() {
    if (!pathFindingResultData) return;

    const group = pathData.groups[groupIndex];
    const floorResult = pathFindingResultData.floors[group.id];
    if (!floorResult) return;

    const points = floorResult.nodes;
    if (!points || points.length < 2) return;

 

    // advance by fixed world-space distance
    distanceTraveled = Math.min(distanceTraveled + speed, totalPathLength);
    // find current segment
    let segment = 0;
    while (segment < segmentLengths.length && cumulativeLengths[segment + 1] < distanceTraveled) {
        segment++;
    }
    const segmentStart = cumulativeLengths[segment];
    const segLen = segmentLengths[segment] || 0;
    const localT = segLen > 0 ? (distanceTraveled - segmentStart) / segLen : 0;
    const p1 = points[segment];
    const p2 = points[segment + 1];
    const pos = interpolate(p1, p2, localT);

    if (distanceTraveled == totalPathLength == 1) distanceTraveled = 0;
    // clear overlay and draw animated circle with manual projection
    ctxOverlay.setTransform(1, 0, 0, 1, 0, 0);
    ctxOverlay.clearRect(0, 0, canvasOverlay.width, canvasOverlay.height);
    const rect = canvasMain.getBoundingClientRect();
    const dpr = window.devicePixelRatio || 1;
    const pivotX = (rect.width / 2 + pan.x) * dpr;
    const pivotY = (rect.height / 2 + pan.y) * dpr;

 
    // world → scaled
    const xs = pos.x * scale;
    const ys = pos.y * scale;
    // rotated
    const cosR = Math.cos(rotation);
    const sinR = Math.sin(rotation);
    const xr = xs * cosR - ys * sinR;
    const yr = xs * sinR + ys * cosR;
    // translated
    const screenX = xr + pivotX;
    const screenY = yr + pivotY;
    const radius = 4;
    ctxOverlay.beginPath();
    ctxOverlay.arc(screenX, screenY, radius, 0, Math.PI * 2);
    ctxOverlay.fillStyle ='red';
    ctxOverlay.fill();




    if (distanceTraveled < totalPathLength) {
        requestAnimationFrame(animate);
    }
}



/* ---------- pick an alpha based on zoom ---------- */
function labelAlpha(scale) {
    if (scale >= 9) return 1;                // full opacity
    if (scale <= 7) return 0;                // fully hidden
    return (scale - 7) / 0.5;                // 3.0‑→‑3.5  lerp 0→1
}
const iconCache = new Map();     // filename -> { img, loaded }

function loadIcon(filename) {
    if (iconCache.has(filename)) return iconCache.get(filename);

    const img = new Image();
    img.crossOrigin = "anonymous";
    img.src = `https://cdn.jsdelivr.net/gh/elseforty/IndoorLens/LocationIcons/${filename}`;   // <- use LocationIcons here
    const entry = { img, loaded: false };
    img.onload = () => {
        entry.loaded = true;
        drawMainCanvas();                                   // redraw when it finishes
    };
    iconCache.set(filename, entry);
    return entry;
}

function iconNameFor(loc) {
    switch (LocationTypeName[loc.locationType]) {
        case "Exit": return "Exit.png";
        case "Entrance": return "Entrance.png";
        case "Restroom": return "Restroom.png";
        case "Elevator": return "Elevator.png";
        case "FireExtinguisher": return "FireExtinguisher.png";
        case "Kiosk": return "Kiosk.png";
        case "Information": return "Information.png";
        case "Current_Kiosk": return "KioskCurrent.png";
        case "MedicalKit": return "MedicalKit.png";
        case "Stairs": return "Stairs.png";
        case "Cafeteria": return "Cafeteria.png";
    }
}


function drawMapLocationSurface(mapLocation, color) {
    if (mapLocation == null) return;
    for (var i = 0; i < mapLocation.surfaceBranchKeys.length; i++) {
        var branchKey = mapLocation.surfaceBranchKeys[i];
        var branch = pathData.branches[branchKey];
        if (branch == null) return;

        const verts = branch.nodes.map(n => ({ x: n.position.x, y: n.position.z }));
        ctx.fillStyle = hexToRgba(color, 0.3);

        ctx.beginPath();
        ctx.moveTo(verts[0].x, verts[0].y);
        verts.slice(1).forEach(p => ctx.lineTo(p.x, p.y));
        ctx.closePath();
        ctx.fill();
    }


}
function hexToRgba(hex, alpha = 1) {
    const r = parseInt(hex.slice(1, 3), 16);
    const g = parseInt(hex.slice(3, 5), 16);
    const b = parseInt(hex.slice(5, 7), 16);
    return `rgba(${r}, ${g}, ${b}, ${alpha})`;
}

function drawSurface(layer, group) {
    for (const branch of Object.values(layoutData.branches)) {
        if (branch.groupId !== group.id || branch.layerId !== layer.id) continue;

        const verts = branch.nodes.map(n => ({ x: n.position.x, y: n.position.z }));
        ctx.fillStyle = layer.color;
        ctx.beginPath();
        ctx.moveTo(verts[0].x, verts[0].y);
        verts.slice(1).forEach(p => ctx.lineTo(p.x, p.y));
        ctx.closePath();
        ctx.fill();
    }
}

function drawLine(layer, group) {
    for (const branch of Object.values(layoutData.branches)) {
        if (branch.groupId !== group.id || branch.layerId !== layer.id) continue;

        const verts = branch.nodes.map(n => ({ x: n.position.x, y: n.position.z }));
        ctx.lineWidth = layer.lineThickness;
        ctx.strokeStyle = layer.color;
        ctx.beginPath();
        ctx.moveTo(verts[0].x, verts[0].y);
        verts.slice(1).forEach(p => ctx.lineTo(p.x, p.y));
        ctx.stroke();
    }
}
drawMainCanvas();

export function getGroupIndex(groupId) {
    if (pathData == null) return -1;
    for (var g = 0; g < pathData.groups.length; g++) {
        var group = pathData.groups[g];
        if (group.id == groupId) return g;
    }
    return -1;
}

function getMapLocation(mapLocationId) {
    if (mapData == null) return null;
    for (var l = 0; l < mapData.locations.length; l++) {
        var mapLocation = mapData.locations[l];
        if (mapLocation.id === mapLocationId) return mapLocation;
    }
    return null
}

export function setMapData(v) {
    mapData = new MapData(v);
    const badgeSpan = document.querySelector('#locationBadge span');
    badgeSpan.textContent = mapData.name;
    drawMainCanvas();              // if you want an immediate redraw
}

export function setLayoutData(v) {
    layoutData = new SplineData(v);
    drawMainCanvas();              // if you want an immediate redraw
}

export function setPathData(v) {
    pathData = new SplineData(v);
    drawMainCanvas();              // if you want an immediate redraw
}



export function setPathFindingResultData(v) {
    if (v == null) { pathFindingResultData = null; return; }
    pathFindingResultData = new PathFindingResultData(v);

    updateDirections();
    computePathMetrics();
    animate();
}

function computePathMetrics() {
    segmentLengths = [];
    cumulativeLengths = [0];
    const group = pathData.groups[groupIndex];
    const floorResult = pathFindingResultData.floors[group.id];
    if (!floorResult) { totalPathLength = 0; return; }
    const pts = floorResult.nodes;
    for (let i = 0; i < pts.length - 1; i++) {
        const dx = pts[i + 1].x - pts[i].x;
        const dy = pts[i + 1].z - pts[i].z;
        const len = Math.hypot(dx, dy);
        segmentLengths.push(len);
        cumulativeLengths.push(cumulativeLengths[i] + len);
    }
    totalPathLength = cumulativeLengths[cumulativeLengths.length - 1] || 0;
    distanceTraveled = 0;
}


function getNextEntry(dict, currentKey) {
    const keys = Object.keys(dict);
    const idx = keys.indexOf(currentKey);
    if (idx >= 0 && idx + 1 < keys.length) {
        const nextKey = keys[idx + 1];
        return nextKey;
    }
    return undefined;  // no next entry
}

export function updateDirections() {
    if (pathFindingResultData == null) return;
    const banner = document.getElementById('directionsBanner');
    const directionsText = document.getElementById('directions');

    var group = pathData.groups[groupIndex];
    var floorResult = pathFindingResultData.floors[group.id];

    //user is on the wrong floor, urge user to navigate to the right floor
    if (!floorResult) {
        const keys = Object.keys(pathFindingResultData.floors);
        if (keys.length != 0) {
            const nextFloorIndex = getGroupIndex(keys[0]);
            let message = `Please switch map to ${getFloorString(nextFloorIndex)}.`;
            directionsText.textContent = message;
            banner.classList.remove('hidden');
        } else {
            banner.classList.add('hidden');
        }
        return;
    }
    var fromMapLocation = getMapLocation(floorResult.fromId);
    var toMapLocation = getMapLocation(floorResult.toId);
    if (fromMapLocation == null || toMapLocation == null) return;
    const fromMapLocationName = fromMapLocation.name ? fromMapLocation.name : LocationTypeName[fromMapLocation.locationType];
    const toMapLocationName = toMapLocation.name ? toMapLocation.name : LocationTypeName[toMapLocation.locationType];


    let distance = '';
    //Units conversion
    if (window.IndoorLens.unit == 'm') {
        distance = floorResult.pathLength.toFixed(1) + ' m';
    } else {
        distance = (floorResult.pathLength * 3.28084).toFixed(1) + ' feet';;
    }

    // --- usage example ---
    const floors = pathFindingResultData.floors;
    const curKey = group.id;           // e.g. "a19282bc"
    const nextFloorResult = getNextEntry(floors, curKey);
    const nextFloorIndex = getGroupIndex(nextFloorResult);

    let message = '';
    if (nextFloorResult != undefined) {
        message = `On ${getFloorString(groupIndex)}, from ${fromMapLocationName} ${distance} to ${toMapLocationName}. then to the ${getFloorString(nextFloorIndex)}.`;

    } else {
        message = `On ${getFloorString(groupIndex)}, from ${fromMapLocationName} ${distance} to ${toMapLocationName}.`;
    }


    directionsText.textContent = message;
    banner.classList.remove('hidden');

    drawMainCanvas();
}

export function getFloorString(floorIndex) {
    if (floorIndex == 0) return 'ground floor';
    else if (floorIndex == 1) return 'first floor';
    else if (floorIndex == 2) return 'second floor';
    else if (floorIndex == 3) return 'third floor';
    else if (floorIndex == 4) return 'fourth floor';
    else if (floorIndex == 5) return 'fifth floor';
    else if (floorIndex == 6) return 'sixth floor';
    else if (floorIndex == 7) return 'seventh floor';
    else if (floorIndex == 8) return 'eighth floor';
    else if (floorIndex == 9) return 'ninth floor';
    else if (floorIndex == 10) return 'tenth floor';
}


class IndoorLensWidget {
    constructor(root, opts) {
        this.root = root;          // <div> host element
        this.opts = opts;
        // mapId = opts.mapid;
        this.setupDom();             // create canvas, overlay, inputs
        this.initLogic();            // attach listeners, load data
    }
}